import * as React from 'react';
import {
  EmbedPlugin,
} from '@ali/4ever-bamboo';
import { ZhiConfig } from '../../../types';
import Audio from './audio';

const { EmbedFailedCard } = EmbedPlugin;

export default function createRenderNode(config: ZhiConfig) {
  const audioConfig = config.audio || {};
  const {
    locale: audioLocaleConfig = {},
    downloadFile,
    onMediaLoadSuccess,
    onMediaLoadError,
    transformAudioURL,
    allowDownload,
    onPreview,
  } = audioConfig;

  return {
    embed: (props, controller, next) => {
      const { node } = props;

      if (node.data.type === 'audio' && node.data.errorType) {
        return <EmbedFailedCard node={node} locale={audioLocaleConfig} />;
      }

      if (node.data.type === 'audio' && node.data.src) {
        return (
          <Audio
            {...props}
            controller={controller}
            onDownload={downloadFile}
            locale={audioLocaleConfig}
            onMediaLoadSuccess={onMediaLoadSuccess}
            onMediaLoadError={onMediaLoadError}
            transformAudioURL={transformAudioURL}
            allowDownload={allowDownload}
            onPreview={onPreview}
          />
        );
      }
      return next();
    },
  };
}
